import React from "react";
import { Helmet } from 'react-helmet';

import './index.sass';

import { Header } from '../components/Header/Header';
import { MainPage } from '../components/MainPage/MainPage';

const Home = () => (
  <div>
    <Helmet>
      <meta charSet="utf-8" />
      <meta name="description" content="Site for fun" />
      <meta name="keywords" content="HTML, CSS, JavaScript, frontend" />
      <meta name="author" content="https://github.com/tymino" />
      <title>tymino</title>
    </Helmet>
    <Header />
    <MainPage />
  </div>
);

export default Home;